import React, { useEffect, useState } from "react"

type TextInputProps = {
  title: string
  initialText: string
  inputType?: React.HTMLInputTypeAttribute | undefined
  returnValue: (text: string) => void
}

function TextInput({ title, initialText, inputType, returnValue }: TextInputProps) {
  const [text, setText] = useState(initialText)
  const inputRef = React.createRef<HTMLInputElement>()

  //     
  useEffect(() => {
    setText(initialText)
  }, [initialText])

  //      0    
  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    inputType === "number"
      ? setText(e.target.value.replace(/^0+/, "").replace(/\D/, ""))
      : setText(e.target.value)
  }

  //     
  const handleBlur = () => {
    returnValue(text)
  }

  //   
  const checkEnterPressed = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === "Enter") {
      inputRef.current?.blur()
    }
  }

  return (
    <div>
      <h3>{title}</h3>
      <input
        ref={inputRef}
        value={text}
        onChange={onChange}
        onBlur={handleBlur}
        onKeyDown={checkEnterPressed}
      />
    </div>
  )
}

export default TextInput
